body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #2C3E50;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4CA1AF, #2C3E50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: 'Poppins';
}

.lineaTiempo{
  border-bottom: 1px solid white;
  display: inline-block;
  width: 65%;
  position: relative;
}

.fechasLinea{
  border: 1px solid black;
  padding: 25px;
  margin: 10px 20px;
  text-align: center;
  background: rgba(4, 37, 53, 0.795);
  color: white;
}
.fechasLinea h2{
  margin: 0 auto;
}
input{
  width: 5em;
  margin: 3px auto;
  display: block;
  outline: none;
  background: none;
  border: none;
  background: white;
  padding: 5px 8px;
  font-family: 'Poppins', sans-serif;
  border-bottom: 3px solid rgb(39, 166, 216);
}
.fechasLinea div{
  text-align: center;
}

.formAdd h1{
  font-size: 25px;
}
.formAdd{
  padding: 15px;
  border: 1px solid black;
  margin: 15px 20px;
  margin-bottom: 250px;
  background: rgba(4, 37, 53, 0.795);
  color: white;
  text-align: center;
}
.formAdd input, .formAdd label{
  display: inline-block;
  width: 50%;
}
.formAdd input{
  padding: 5px;
  outline: none;
}
.formAdd button{
  display: block;
  margin: 10px auto;
  border: none;
  outline: none;
  background: white;
  cursor: pointer;
  padding: 5px 10px;
  border: 2px solid rgb(39, 166, 216);
  transition: all 0.4s ease;
}
.formAdd button:hover{
  color: white;
  background: transparent;
}

#indicacion{
  text-decoration: underline;
}
.espacioTrabajo{
  margin-bottom: 50px;
  text-align: center;
}
.espacioTrabajo > span{
  margin: 0 15px;
  color: white;
  background: rgba(0, 0, 0, 0.692);
  padding: 5px;
  cursor: pointer;
}
@media screen and (max-width:425px){
  .espacioTrabajo > span{
    font-size: 12px;
  }
}

.elemento .contenido{
  width: 130px;
  text-align: center;
  height: 230px;
  border: 1px solid rgba(255, 255, 255, 0.603);
}
.elemento .img{
  height: 60px;;
}
.elemento img{
  max-width: 100%;
  max-height: 100%;
}
.elemento .contenido h6{
  margin: 0;
  font-size: 14px;
  clear: both;
  overflow: auto;
  max-height: 60px;
}

.contenido button{
  display: block;
  margin: 10px auto;
  border: none;
  outline: none;
  background: white;
  cursor: pointer;
  padding: 5px 10px;
  border: 2px solid rgb(39, 166, 216);
  transition: all 0.4s ease;
}
.contenido button:hover{
  color: white;
  background: transparent;
}
.elemento .fechaElemento{
  display: block;
  border-bottom: none;
  margin: 10px auto;
  font-size: 14px;
}
.contenedorElemento{
  display: inline-block;
  position: absolute;
  bottom: 15px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: rgba(4, 37, 53, 1);
  color: white;
}
.contenedorElemento .fas{
  cursor: pointer;
  float: right;
  margin: 10px;
  border: none;
}
.contenedorElemento .fas:hover{
  color: black;
  background: white;
  
}



.punto{
  display: inline-block;
  position: absolute;
  bottom: -10px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.punto > i{
  color: white;
  font-size: 10px;
  transition: all 0.4s ease;
  cursor: pointer;
}
.punto > i:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

